import { useParams, useHistory } from "react-router-dom";
import { useState } from "react";
import useFetch from "../hooks/useFetch";
import firebase from "../../firebase";
import "./blogDetails.css";

const BlogDetails = () => {
   const { id } = useParams();
   const history = useHistory();
   const [blog, setBlog] = useState(null);
   const [updated, setUpdated] = useState(false);
   const { isLoading, error } = useFetch("blog1");

   const db = firebase.firestore().collection("blog1");
   db.doc(id)
      .get()
      .then((doc) => {
         setBlog(doc.data());
      });

   const deleteBlog = () => {
      db.doc(id)
         .delete()
         .then(() => {
            history.push("/dojoBlog");
         });
   };

   const editBlog = () => {
      let editIcon = document.querySelector(".editIcon");
      //.elements to be edited
      let blogBody = document.querySelector("#blogBody");
      let blogTitle = document.querySelector("#title");

      const blogElements = [blogBody, blogTitle];

      if (editIcon.textContent === "edit") {
         for (let i = 0; i < blogElements.length; i++) {
            blogElements[i].contentEditable = true;
         }
         editIcon.textContent = "check";
         alert("You can edit the blog now.");
      } else if (editIcon.textContent === "check") {
         setUpdated(true);
         for (let i = 0; i < blogElements.length; i++) {
            blogElements[i].contentEditable = false;
         }
         db.doc(id)
            .update({
               body: blogBody.textContent,
               title: blogTitle.textContent,
            })
            .then(() => {
               setUpdated(false);
            });
         editIcon.textContent = "edit";
      }
   };

   return (
      <div className="blog-details">
         {(updated || isLoading) && (
            <div className="progress">
               <div className="indeterminate pink accent-3-only"></div>
            </div>
         )}
         {error && (
            <div style={{ fontSize: "20px", fontWeight: "700" }}>{error}</div>
         )}

         {blog && !updated && (
            <article>
               <h2>
                  <span id="title">{blog.title}</span>
                  <i
                     className="material-icons right deleteIcon"
                     style={{
                        cursor: "pointer",
                        fontSize: "40px",
                     }}
                     onClick={deleteBlog}
                  >
                     delete
                  </i>
               </h2>
               <h6>
                  <span id="author">Written by {blog.author}</span>
                  <i className="material-icons editIcon" onClick={editBlog}>
                     edit
                  </i>
               </h6>
               <div>
                  <p id="blogBody">
                     <b>{blog.body}</b>
                  </p>
               </div>
            </article>
         )}
      </div>
   );
};

export default BlogDetails;
